import React from 'react';
import MyStack from './MyStack';
import Experience_n_Cert from './Experience_n_Cert';
import { LazyLoadImage } from "react-lazy-load-image-component";
import 'react-lazy-load-image-component/src/effects/blur.css';

const aboutme = () => {
    return (
        <section id = "about_me" className="p-4">
            <div className="mt-24 text-center">
                <span className=" font-bold text-2xl pt-4 border-b-2 border-gray-400" style={{color: '#091c29'}}>ABOUT ME</span>
            </div>          
            
            <p className="font-bold text-center text-xl pt-2 mt-2 text-gray-600">Profile</p>
            <div className="shadow-lg lg:flex justify-center m-auto md:mx-24">
                 
                <div className="flex flex-wrap text-white p-2 justify-evenly items-center"  style={{backgroundColor: '#091c29'}}>
                    <div className="flex justify-center items-center p-2 w-64 h-64">
                        <LazyLoadImage className="lg:rounded-full w-full h-full shadow-2xl" src="/images/header_image4.jpg" effect="blur"/>
                    </div>               
                    <div className="flex flex-col justify-evenly mx-0">
                        <div className="my-2"><span className="font-bold">Full Name : </span><span>Onyeaka, Gabriel Nonso</span></div>
                        <div className="my-2"><span className="font-bold">Email : </span><span>gabriel.onyeaka@gmail.com</span></div>
                        <div className="my-2"><span className="font-bold">Phone : </span><span>08021072445</span></div>
                        <div className="my-2"><span className="font-bold">Nationality : </span><span>Nigeria</span></div>
                    </div>
                </div>
                <div className="mx-4 sm:pr-4 my-2">
                    <div className="text-justify leading-relaxed"> 
                        <p className="md:text-lg text-gray-700">Hi, I'm a Web developer from Anambra State, Nigeria. I work with front-end and back-end web technologies to design responsive web pages, create engaging web interfaces for great web user experience and solve problems for individuals and enterprise by developing useful web contents and applications. But I've mostly worked on back-end.</p>
                        <p className="md:text-lg text-gray-700">I also debug and maintain codes, I'm knowledgeable in OOP, I use git and i'm also familiar with relational databases.</p>
                        <p  className="md:text-lg text-gray-700">I'm passionate about what i do, value teamwork and love to engage my mind creatively.</p>
                        <p className="md:text-lg text-gray-700">I'm inspired to make web and technology a leverage for anyone, by making it convenient and easy to use for whatsoever purpose.</p>
                    </div>
                    <div className="flex flex-col my-4 items-center">
                        <p className="flex text-gray-400 text-md md:text-left text-center">{MyStack.stack.title} <span className="ml-2"> {MyStack.stack.title_icon}</span></p>
                        <ul className="flex flex-wrap items-center justify-center">
                            {MyStack.stack.tech.map((tech,index) => {
                                return (
                            <li className={`${index % 2 === 0 ? " animate-float" : " animate-refloat"} m-1 rounded-full w-20 h-20 flex flex-col items-center justify-center bg-white shadow-lg`}>
                                <div className="font-bold text-xs mb-0 pb-0">{tech.icon_txt}</div>
                                <div className="mt-0 pt-0">
                                    <span className={tech.style + " text-3xl"}  >{tech.icon}</span>
                                </div>
                            </li>
                                        )
                            })}
                        </ul>
                    </div>
                </div> 
            </div>
            <Experience_n_Cert />
        </section>
    )
}

export default aboutme
