import React from 'react';
import { useState } from "react";
import { FaTimes, FaBars } from 'react-icons/fa';


const Nav = () => {

    const [navbarOpen, setNavbarOpen] = useState(false);
    
    const handleToggle = () => {
    /* setNavbarOpen(prev => !prev) */
    setNavbarOpen(!navbarOpen)
    }

    return (
            <nav className="fixed flex items-center justify-between mx-auto w-full top-0 h-16 text-white p-6 shadow-md z-50" style={{background: '#091c29'}}>
                <div className="logo text-3xl font-bold">
                    <h2>Gabrielonso <span className="w-3 h-3 bg-blue-500 inline-block rounded-full"></span></h2>
                </div>

                <div className={(navbarOpen ? "block" :
                "hidden") + " fixed top-16 w-full left-0 md:w-auto md:flex md:relative md:top-0 justify-between"}>
                    <ul className=" text-center  font-bold md:flex"  style={{background: '#091c29'}}>
                        <li className="hover:bg-gray-800 hover:text-blue-500 transition"><a href="#home" className="p-4 block m-0">Home</a></li>
                        <li className="hover:bg-gray-800 hover:text-blue-500"><a href="#about_me" className="p-4 block m-0">About Me</a></li>
                        <li className="hover:bg-gray-800 hover:text-blue-500"><a href="#services" className="p-4 block m-0">Services</a></li>
                        <li className="hover:bg-gray-800 hover:text-blue-500"><a href="#portfolio" className="p-4 block m-0">Portfolio</a></li>
                        <li className="hover:bg-gray-800 hover:text-blue-500"><a href="#contact" className="p-4 block m-0">Contact</a></li>
                    </ul>
                </div>
                    
                <button onClick={handleToggle} className="text-2xl md:hidden mx-4">
                    {navbarOpen ? <FaTimes/> : <FaBars/>}
                </button>
            </nav>
    )
}

export default Nav
