import React from 'react';
import Nav from './Nav';
import { LazyLoadImage } from "react-lazy-load-image-component";
import 'react-lazy-load-image-component/src/effects/blur.css';

const header = () => {
    return (
        <header className="w-full h-full" id="home">
                <Nav />
                <div className="flex justify-center mt-16 h-full w-full relative" style={{background: '#091c29'}}>
                    
                    <div className="flex flex-col justify-between md:flex-row-reverse items-center mx-auto md:w-11/12">
                        <div className="md:w-2/5 opacity-25 z-0 mx-auto p-0">
                            <LazyLoadImage className="" src="/images/header_image6.jpg" effect="blur"/>
                        </div>
                        <div className="text-white z-10 mx-auto p-8 md:p-16 absolute md:relative top-56 md:top-auto tracking-wider">
                            <h6 className="text-3xl md:text-4xl font-bold">Gabriel Nonso</h6>
                            <p className="md:text-lg">A Web Developer with passion for creating interesting and useful web resources/solutions to meet the needs of individuals and enterprises</p>
                            <div className="text-center m-4">
                                <a href="#about_me" className="inline-block bg-gray-800 p-4 rounded shadow-2xl hover:bg-gray-900">More about me</a>
                            </div>
                            
                        </div>
                    </div>
                    
                </div>
                
        </header>
    )
}
export default header