import React from 'react';
import {MdDevices} from 'react-icons/md';
import { FcSmartphoneTablet } from "react-icons/fc";
import { DiAppstore } from "react-icons/di";

const Services = () => {
    return (
        <section id="services" className="m-auto md:px-4 md:mx-24 xl:h-screen pt-24" style={{color: '#091c29'}}>
            <div className="text-center p-4">
                <span className="text-center font-bold border-b-2 border-gray-400 text-2xl">SERVICES</span>
            </div>
            
            <div className="flex flex-wrap justify-center items-center">
                <div className="flex flex-col items-center justify-center text-center rounded w-80 h-64 m-4 p-4" style={{boxShadow: '5px 5px 5px 0px #091c29'}}>
                    < MdDevices className="text-6xl" />
                    <p className="font-bold border-b-2">Web Development</p>
                    <p className="p-2 m-2 h-44 leading-relaxed">Build solutions to help you reach out to far more audience, advertise and share information, gain credibility from potential clients and customers and also, help you manage your business</p>
                </div>
                <div className="flex flex-col items-center justify-center text-center rounded w-80 h-64 m-4 p-4" style={{boxShadow: '5px 5px 5px 0px #091c29'}}>
                    <DiAppstore className="text-6xl" />
                    <p className="font-bold border-b-2">UI/UX Designs</p>
                    <p className="p-2 m-2 h-44 leading-relaxed">Keep users engaged when surfing your products and services online by creating satisfying web interfaces for best user experience</p>
                </div>
                <div className="flex flex-col items-center justify-center text-center rounded w-80 h-64 m-4 p-4" style={{boxShadow: '5px 5px 5px 0px #091c29'}}>
                    <FcSmartphoneTablet className="text-6xl" />
                    <p className="font-bold border-b-2">Responsive web designs</p>
                    <p className="p-2 m-2 h-44 leading-relaxed">Design responsive web pages to suit various screen sizes</p>
                </div>
                
            </div>
        </section>
    )
}

export default Services
