import React from 'react';
import { useState } from "react";
import { LazyLoadImage } from "react-lazy-load-image-component";

const Portfolio = () => {
    const [seeMore, showContents] = useState(false);
    const visibilityToggle = () => {
    /* setNavbarOpen(prev => !prev) */
    showContents(!seeMore);
    }
    return (
        <section id="portfolio" className="m-auto sm:px-4 md:mx-24 xl:h-screen pt-24" style={{color: '#091c29'}}>
            <div className="text-center p-4">
                <span className="text-center font-bold border-b-2 border-gray-400 text-2xl">PORTFOLIO</span>
            </div>
            <div className="flex flex-wrap justify-center items-center mt-4">
                <div className="xl:flex text-center justify-center items-center sm:w-2/3 relative m-4" style={{boxShadow: 'inset 0 0 20px steelblue'}}>
                    <div className="xl:w-1/3 h-64">
                        <LazyLoadImage className="h-64 w-80" src="/images/mystoremanager.png" effect="blur"/>
                    </div>
                    <p className="xl:w-2/3 leading-relaxed p-2"><div className="font-bold">mystoremanager</div>A responsive web application developed with HTML, CSS, JavaScript, Bootstrap, PHP, JQuery and MySql to <span className={(seeMore ? "inline" :
                "hidden")} >manage and handle certain operations of a retail-wholesaling business, where there are several employees and where inventory and trading of goods are done in two categories </span> <a href="javascript:;" onClick={visibilityToggle} className="text-blue-500 underline"><span>{seeMore ?" see less" : " see more..." }</span></a><br/>
                    <a href="https://github.com/Gabrielonso/mystoremanager.git" target="_blank" className="p-2 m-2 inline-block my-4 bg-yellow-500 hover:bg-yellow-600">View source code</a> <a href="https://mystoremanager.000webhostapp.com" target="_blank" className="p-2 m-2 inline-block my-4 text-white" style={{backgroundColor: '#091c29'}}>Explore project</a></p>
                </div>
            </div>
        </section>
    )
}

export default Portfolio
