import React from 'react';
import {IoIosCall, IoIosMail,IoMdPin,IoLogoFacebook,IoLogoGithub,IoLogoLinkedin,IoLogoTwitter,IoLogoWhatsapp,} from 'react-icons/io';

const Contact = () => {
    return (
        <section id="contact" className="flex flex-col items-center m-auto sm:px-4 md:mx-24 xl:h-screen pt-24">
            <div className="text-center p-4">
                <span className="text-center font-bold border-b-2 border-gray-400 text-2xl">CONTACT</span>
            </div>
            <div className="flex flex-col md:flex-row md:space-x-6 md:space-y-0 max-w-4xl p-8 sm:p-12 space-y-6 items-center justify-center shadow-lg text-white rounded-xl w-full overflow-hidden"  style={{backgroundColor: '#091c29'}}>
                <div className="flex space-y-8 flex-col justify-between">
                    <div>
                        <h1 className="font-bold text-4xl tracking-wide">Get in touch</h1>
                        <p className="pt-2">Reach me on...</p>
                    </div>
                    <div className="flex flex-col space-y-4">
                        <div className="inline-flex space-x-2 items-center">
                            <IoIosCall className="text-xl" />
                            <span>08021072445</span>
                        </div>
                        <div className="inline-flex space-x-2 items-center">
                            <IoIosMail className="text-xl" />
                            <span>gabriel.onyeaka@gmail.com</span>
                        </div><div className="inline-flex space-x-2 items-center">
                            <IoMdPin className="text-xl" />
                            <span>Abuja, Nigeria</span>
                        </div>
                    </div>
                    <div className="flex space-x-4 text-lg">
                        <a href="#"><IoLogoFacebook /></a>
                        <a href="#"><IoLogoGithub /></a>
                        <a href="#"><IoLogoLinkedin /></a>
                        <a href="#"><IoLogoTwitter /></a>
                        <a href="#"><IoLogoWhatsapp /></a>
                    </div>
                </div>
                <div className="relative">
                    <div className="absolute w-40 h-40 rounded-full bg-blue-500 -right-28 -top-28"></div>
                    <div className="absolute w-40 h-40 rounded-full bg-blue-500 -left-28 -bottom-16"></div>
                    <div className="relative z-10 bg-white rounded-xl shadow-lg p-8 text-gray-600 md:w-80">
                        <form action="" className="flex flex-col space-y-4">
                            <div>
                                <label for="" className="text-sm">Name</label>
                                <input type="text" name="name" placeholder="Enter your name..." className="ring-1 ring-gray-300 w-full rounded-md px-4 py-2 outline-none focus:ring-2 focus:ring-blue-300"/>
                            </div>
                            <div>
                                <label for="" className="text-sm">Email</label>
                                <input type="email" name="email" placeholder="Enter your email..." className="mt-2 ring-1 ring-gray-300 w-full rounded-md px-4 py-2 outline-none focus:ring-2 focus:ring-blue-300"/>
                            </div>
                            <div>
                                <label for="" className="text-sm">Message</label>
                                <textarea name="message" placeholder="Enter your message..."
                                rows="4" className="mt-2 ring-1 ring-gray-300 w-full rounded-md px-4 py-2 outline-none focus:ring-2 focus:ring-blue-300"></textarea>
                            </div>
                            <button className="inline-block self-end text-white bg-blue-500 font-bold rounded-lg px-6 py-2 uppercase text-sm">send message</button>
                        </form>
                    </div>
                    
                </div>
                
            </div>
        </section>
    )
}

export default Contact
